const gallEl = document.querySelector('.gallery');

function pictureTemplate(pic) {
    return `<li class="gallery-item">
        <a class="gallery-link" href="${pic.original}">
            <img
                width="360" 
                height="200"
                class="gallery-image"
                src="${pic.preview}"
                alt="${pic.description}"
            />
        </a>
    </li>`;
}

function picturesTemplate(pics) {
    return pics.map(pictureTemplate).join('\n')
}

const result = picturesTemplate(images);

gallEl.insertAdjacentHTML('afterbegin', result);

gallEl.addEventListener('click', (e) => {
    e.preventDefault();

    if (e.currentTarget === e.target) {
        return;
    };
})

import SimpleLightbox from "simplelightbox";
import "simplelightbox/dist/simple-lightbox.min.css";

let sl = new SimpleLightbox('.gallery a', {
    sourceAttr: 'href',
    overlay: true,
    overlayOpacity: 0.9,
    spinner: true,
    nav: true,
    navText: ['<', '>'],
    captions: true,
    captionsData: 'alt',
    captionDelay: 250,
    showCounter: true,
});

sl.on('error.simplelightbox', function (e) {
    console.log(e);
});